.work-order-analytics {
  padding: 1.5rem;
  background-color: #f8fafc;
  min-height: 100vh;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.loading-state .loading-spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid #e2e8f0;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
.loading-state p {
  color: #64748b;
  font-size: 1.125rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.error-state .error-title {
  color: #dc2626;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 1rem 0;
}
.error-state .btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
}
.error-state .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
  border: none;
}
.error-state .btn.btn-primary:hover {
  background-color: #2563eb;
}

.no-data-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  background: white;
  border-radius: 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.no-data-message .no-data-title {
  color: #374151;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 1rem 0 0.5rem 0;
}
.no-data-message .no-data-description {
  color: #6b7280;
  font-size: 1rem;
  margin: 0;
}

.analytics-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  background: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.analytics-header .header-left .main-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.analytics-header .header-left .subtitle {
  color: #64748b;
  font-size: 1.125rem;
  margin: 0;
}
.analytics-header .header-right .refresh-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background-color: #f3f4f6;
  color: #374151;
  border: none;
  border-radius: 0.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.analytics-header .header-right .refresh-btn:hover {
  background-color: #e5e7eb;
  transform: translateY(-1px);
}

.section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 1rem 0;
}

.section-subtitle {
  color: #64748b;
  font-size: 1rem;
  margin: 0 0 1.5rem 0;
}

.metrics-section {
  margin-bottom: 2rem;
}
.metrics-section .metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}
.metrics-section .metric-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
}
.metrics-section .metric-card .metric-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.metrics-section .metric-card .metric-header .metric-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #64748b;
  margin: 0;
}
.metrics-section .metric-card .metric-header .metric-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #6b7280;
}
.metrics-section .metric-card .metric-value {
  font-size: 2rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 0.5rem;
}
.metrics-section .metric-card .metric-value.warning {
  color: #f59e0b;
}
.metrics-section .metric-card .metric-trend {
  font-size: 0.875rem;
  color: #64748b;
}
.metrics-section .metric-card .metric-trend.positive {
  color: #10b981;
}
.metrics-section .metric-card .metric-target {
  font-size: 0.875rem;
  color: #64748b;
}
.metrics-section .metric-card .metric-status {
  font-size: 0.875rem;
  color: #10b981;
  font-weight: 500;
}

.charts-section {
  margin-bottom: 2rem;
}
.charts-section .chart-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}
.charts-section .chart-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.charts-section .chart-card.full-width {
  grid-column: 1/-1;
}
.charts-section .chart-card .chart-header {
  margin-bottom: 1.5rem;
}
.charts-section .chart-card .chart-header .chart-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.charts-section .chart-card .chart-header .chart-subtitle {
  color: #64748b;
  font-size: 0.875rem;
  margin: 0;
}
.charts-section .chart-card .chart-container {
  min-height: 300px;
}
.charts-section .pie-chart {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.charts-section .pie-chart .chart-legend {
  flex: 1;
}
.charts-section .pie-chart .chart-legend .legend-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.75rem;
}
.charts-section .pie-chart .chart-legend .legend-item .legend-color {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}
.charts-section .pie-chart .chart-legend .legend-item .legend-label {
  font-weight: 500;
  color: #374151;
  min-width: 80px;
}
.charts-section .pie-chart .chart-legend .legend-item .legend-value {
  font-weight: 600;
  color: #1e293b;
  min-width: 30px;
}
.charts-section .pie-chart .chart-legend .legend-item .legend-percentage {
  color: #64748b;
  font-size: 0.875rem;
}
.charts-section .pie-chart .chart-placeholder {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.charts-section .pie-chart .chart-placeholder .chart-circle {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: conic-gradient(#10b981 0deg 174deg, #ef4444 174deg 234deg, #3b82f6 234deg 264deg, #f59e0b 264deg 285deg, #6b7280 285deg 360deg);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.charts-section .pie-chart .chart-placeholder .chart-circle::before {
  content: "";
  position: absolute;
  width: 120px;
  height: 120px;
  background: white;
  border-radius: 50%;
}
.charts-section .pie-chart .chart-placeholder .chart-circle .chart-center {
  text-align: center;
  z-index: 1;
}
.charts-section .pie-chart .chart-placeholder .chart-circle .chart-center .total-count {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  color: #1e293b;
}
.charts-section .pie-chart .chart-placeholder .chart-circle .chart-center .total-label {
  font-size: 0.875rem;
  color: #64748b;
}
.charts-section .bar-chart .chart-bars .bar-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.charts-section .bar-chart .chart-bars .bar-item .bar-label {
  min-width: 60px;
  font-weight: 500;
  color: #374151;
  font-size: 0.875rem;
}
.charts-section .bar-chart .chart-bars .bar-item .bar-container {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.charts-section .bar-chart .chart-bars .bar-item .bar-container .bar {
  height: 2rem;
  border-radius: 0.25rem;
  min-width: 20px;
  transition: width 0.3s ease;
}
.charts-section .bar-chart .chart-bars .bar-item .bar-container .bar-value {
  font-weight: 600;
  color: #1e293b;
  min-width: 30px;
}
.charts-section .trend-chart {
  display: flex;
  height: 300px;
}
.charts-section .trend-chart .chart-y-axis {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 1rem;
  font-size: 0.75rem;
  color: #6b7280;
  font-weight: 500;
}
.charts-section .trend-chart .chart-area {
  flex: 1;
  position: relative;
}
.charts-section .trend-chart .chart-area .grid-lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #f3f4f6;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line:nth-child(1) {
  top: 0;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line:nth-child(2) {
  top: 25%;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line:nth-child(3) {
  top: 50%;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line:nth-child(4) {
  top: 75%;
}
.charts-section .trend-chart .chart-area .grid-lines .grid-line:nth-child(5) {
  top: 100%;
}
.charts-section .trend-chart .chart-area .trend-lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.charts-section .trend-chart .chart-area .trend-lines .trend-line {
  position: absolute;
  width: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(59, 130, 246, 0.1) 100%);
}
.charts-section .trend-chart .chart-area .trend-lines .trend-line.created {
  background: linear-gradient(90deg, transparent 0%, rgba(59, 130, 246, 0.2) 100%);
}
.charts-section .trend-chart .chart-area .trend-lines .trend-line.completed {
  background: linear-gradient(90deg, transparent 0%, rgba(16, 185, 129, 0.2) 100%);
}
.charts-section .trend-chart .chart-area .chart-x-axis {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
  font-size: 0.75rem;
  color: #6b7280;
  font-weight: 500;
}

.technician-section {
  margin-bottom: 2rem;
}
.technician-section .technician-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}
.technician-section .technician-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.technician-section .technician-card .tech-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.technician-section .technician-card .tech-header .tech-name {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0;
}
.technician-section .technician-card .tech-header .tech-score {
  font-size: 1.5rem;
  font-weight: 700;
  color: #3b82f6;
}
.technician-section .technician-card .tech-metrics {
  margin-bottom: 1rem;
}
.technician-section .technician-card .tech-metrics .tech-metric {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.technician-section .technician-card .tech-metrics .tech-metric .metric-label {
  color: #64748b;
  font-size: 0.875rem;
}
.technician-section .technician-card .tech-metrics .tech-metric .metric-value {
  font-weight: 600;
  color: #1e293b;
}
.technician-section .technician-card .performance-bar {
  height: 0.5rem;
  background-color: #f3f4f6;
  border-radius: 0.25rem;
  overflow: hidden;
}
.technician-section .technician-card .performance-bar .bar-fill {
  height: 100%;
  background-color: #3b82f6;
  border-radius: 0.25rem;
  transition: width 0.3s ease;
}

.insights-section {
  margin-bottom: 2rem;
}
.insights-section .insights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}
.insights-section .insight-card {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-left: 4px solid;
}
.insights-section .insight-card.success {
  border-left-color: #10b981;
}
.insights-section .insight-card.warning {
  border-left-color: #f59e0b;
}
.insights-section .insight-card.info {
  border-left-color: #3b82f6;
}
.insights-section .insight-card.optimization {
  border-left-color: #8b5cf6;
}
.insights-section .insight-card .insight-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.insights-section .insight-card .insight-header .insight-icon {
  font-size: 1.5rem;
}
.insights-section .insight-card .insight-header .insight-tag {
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.insights-section .insight-card .insight-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}
.insights-section .insight-card .insight-description {
  color: #64748b;
  font-size: 0.875rem;
  margin: 0;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .work-order-analytics {
    padding: 1rem;
  }
  .analytics-header {
    flex-direction: column;
    gap: 1rem;
  }
  .analytics-header .header-right {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  .chart-row {
    grid-template-columns: 1fr;
  }
  .pie-chart {
    flex-direction: column;
    gap: 1rem;
  }
  .pie-chart .chart-placeholder .chart-circle {
    width: 150px;
    height: 150px;
  }
  .pie-chart .chart-placeholder .chart-circle::before {
    width: 90px;
    height: 90px;
  }
  .pie-chart .chart-placeholder .chart-circle .chart-center .total-count {
    font-size: 1.5rem;
  }
  .technician-grid,
  .insights-grid {
    grid-template-columns: 1fr;
  }
}
